.cookies-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  width: 100%;
  padding: 10px 15px 10px;
  height: auto;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  text-align: center;
  background-color: $color-black;
  z-index: 99;
  transition: transform 400ms ease-out;
  transform: translateY(100%);
  &.is-active {
    transform: translateY(0);
  }
  @include bp(small) {
    flex-wrap: no-wrap;
    padding: 0 20px;
    height: 60px;
  }
}
