/* Checkbox helpers */
.checkbox {
  margin-bottom: 2rem;
}

.input-hidden,
.checkbox__input-hidden {
  position: absolute;
  opacity: 0;
  z-index: -1;
}

.checkbox__label {
  margin: 0;
  padding-left: 0;
  min-height: inherit;
  width: 100%;
  display: flex;
  align-items: center;
  vertical-align: middle;
  padding-top: 0;
  cursor: pointer;
  text-transform: none;
}

.checkbox__box {
  position: relative;
  max-width: 20px;
  flex: 0 0 20px;
  width: 20px;
  height: 20px;
  border: 2px solid #000000;
  cursor: pointer;
  display: inline-block;
  vertical-align: middle;
  margin-left: 0;
}

.checkbox__text {
  max-width: calc(100% - 20px);
  flex: 0 0 calc(100% - 20px);
  width: calc(100% - 20px);
  display: block;
  position: relative;
  cursor: pointer;
  text-align: left;
  color: #000000;
  font-size: 16px;
  font-size: 1rem;
  padding-left: 1rem;
  line-height: 1.4;
}

.checkbox__slider {
  position: absolute;
  height: 18px;
  width: 18px;
  left: 2px;
  bottom: 2px;
  border-radius: 50% !important;
  background-color: #000000;
  transition: all 0.2s ease;
}

.checkbox__input-hidden:checked + .checkbox__label .checkbox__box:after {
  content: "";
  display: block;
  position: absolute;
  top: 0px;
  left: 5px;
  width: 7px;
  height: 13px;
  border: solid #000000;
  border-width: 0 2px 2px 0;
  transform: rotate(40deg);
}

.checkbox--switcher {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 26px;
}

.checkbox--switcher .checkbox__label {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 2px solid #000000;
  border-radius: 35px;
  background-color: transparent;
}

.checkbox--switcher .checkbox__input-hidden:checked + .checkbox__label {
  border-color: #75439a;
}

.checkbox--switcher .checkbox__input-hidden:checked + .checkbox__label:after {
  display: none;
}

.checkbox--switcher .checkbox__input-hidden:checked + .checkbox__label .checkbox__slider {
  background-color: #75439a;
  left: calc(100% - 20px);
}

.checkbox--radiobox {
  margin-bottom: 2rem;
}

.checkbox--radiobox .checkbox__box {
  top: 0;
  max-width: 20px;
  flex: 0 0 20px;
  width: 20px;
  height: 20px;
  border: 2px solid #000000;
  border-radius: 100%;
}

.checkbox--radiobox .checkbox__input-hidden:checked + .checkbox__label .checkbox__box:after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 100%;
  margin-top: -5px;
  margin-left: -5px;
  width: 10px;
  height: 10px;
  background-color: #000000;
}

