}

.table__row--header {
  height: 50px;
}

.table__row--body {
  height: 100px;
}

.table__row--body:nth-child(even) {
  background-color: #f7f7f7;
}

.table__cell {
  display: table-cell;
  float: none;
  vertical-align: middle;
}

.table__cell--header {
  padding: 10px 5px;
}

@media (min-width: 370px) {
  .table__cell--header {
    padding: 10px 10px;
  }
}

@media (min-width: 771px) {
  .table__cell--header {
    padding: 10px 20px;
  }
}

.table__cell--body {
  padding: 10px 5px;
}

@media (min-width: 370px) {
  .table__cell--body {
    padding: 10px 10px;
  }
}

@media (min-width: 771px) {
  .table__cell--body {
    padding: 10px 20px;
  }
}

.table__text {
  font-size: 12px;
  font-size: 0.75rem;
  line-height: 1.2;
  color: #000000;
}

.table__text--header {
  font-size: 10px;
  font-size: 0.625rem;
  text-transform: uppercase;
}

.table__text--body {
  margin-bottom: 0;
  padding: 10px 0;
  font-size: 16px;
  font-size: 1rem;
  color: #000000;
}

/* Load more content block */
.load-more.is-last .load-more {
  height: auto;
}

.load-more.is-last .load-more__button {
  display: none;
}

.load-more.is-disabled .load-more__button {
  display: none;
}

.load-more__spinner {
  z-index: 1;
  display: none;
}

.load-more__spinner.is-active {
  display: inline-block;
}

.load-more__button {
  cursor: pointer;
}

.cookies-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  width: 100%;
  padding: 10px 15px 10px;
  height: auto;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  text-align: center;
  background-color: #000000;
  z-index: 99;
  transition: transform 400ms ease-out;
  transform: translateY(100%);
}

.cookies-bar.is-active {
  transform: translateY(0);
}

@media (min-width: 771px) {
  .cookies-bar {
    flex-wrap: no-wrap;
    padding: 0 20px;
    height: 60px;
  }
}

.share__item {
  display: inline-block;
  vertical-align: middle;
  margin: 0 15px 15px;
}

.file-upload__input {
  position: absolute;
  z-index: -1;
  overflow: hidden;
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
}

.file-upload__input + .file-upload__label {
  display: inline-block;
  overflow: hidden;
  margin-bottom: 0;
  cursor: pointer;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.file-upload__output {
  display: block;
  margin-top: 10px;
  line-height: 1;
  color: #000000;
  font-size: 16px;
  font-size: 1rem;
}

.file-upload__icon {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 30px;
  margin-left: 10px;
  cursor: pointer;
  vertical-align: middle;
  top: -2px;
}

.file-upload__icon:before, .file-upload__icon:after {
  position: absolute;
  display: block;
  width: 20px;
  height: 2px;
  content: '';
  background: #000;
  top: 50%;
  left: 50%;
  margin-left: -10px;
  margin-top: -1px;
}

.file-upload__icon:before {
  transform: rotate(45deg);
}

.file-upload__icon:after {
  transform: rotate(-45deg);
}

.box {
  position: relative;
  background-color: #ffffff;
  padding: 3rem 2rem 3rem;
}

.box--shadow {
  box-shadow: 0 26px 42px rgba(0, 0, 0, 0.15);
}

.box--border {
  border: 1px solid #000000;
}

.box--size-xsmall {
  padding: 1rem 1.2rem 1rem;
}

.box--size-small {
  padding: 2rem 1.2rem 2rem;
}

.box--size-normal {
  padding: 3rem 1.2rem 3rem;
}

.box--size-big {
  padding: 5rem 1.2rem 5rem;
}
