  }
  .margin-lg-left-auto {
    margin-left: auto;
  }
  .margin-lg-right-auto {
    margin-right: auto;
  }
}

$ways: "top" "right" "bottom" "left";
@each $item in $ways {
  .margin-#{$item}-0 {
    margin-#{$item}: 0 !important;
  }

  .margin-#{$item}-xs-0 {
    @include bp(xsmall) {
      margin-#{$item}: 0 !important;
    }
  }

  .margin-#{$item}-sm-0 {
    @include bp(small) {
      margin-#{$item}: 0 !important;
    }
  }

  .margin-#{$item}-md-0 {
    @include bp(medium) {
      margin-#{$item}: 0 !important;
    }
  }

  .margin-#{$item}-lg-0 {
    @include bp(large) {
      margin-#{$item}: 0 !important;
    }
  }

  @include bp(rev-large) {
    .margin-bp-#{$item}-lg-0 {
      margin-#{$item}: 0 !important;
    }
  }

  @include bp(rev-medium) {
    .margin-bp-#{$item}-md-0 {
      margin-#{$item}: 0 !important;
    }
  }

  @include bp(rev-small) {
    .margin-bp-#{$item}-sm-0 {
      margin-#{$item}: 0 !important;
    }
  }

  @include bp(rev-xsmall) {
    .margin-bp-#{$item}-xs-0 {
      margin-#{$item}: 0 !important;
    }
  }
}
